.navbar {
  position: fixed;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  mix-blend-mode: difference;
  z-index: 1000;
}
.navbar-button-container {
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
  flex-direction: row;
  gap: 42px;
  justify-content: end;
  color: rgb(255 255 255);
  /* mix-blend-mode: difference; */
}

.navbar-button {
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
  flex-direction: row;
  gap: 42px;
  justify-content: end;
}

.navbar-button:hover {
  cursor: pointer;
  background-color: #000000;
  color: rgb(255, 255, 255);
}

.navbar button:hover {
  background-color: #007bff;
  color: #fff;
}
.navbar-button-texts {
  /* font-size: 14px;
  font-family: monospace; */
  font-size: 20px;
  font-family: sans-serif;
  width: 120px;
  overflow: hidden;
}
.navbar-button-texts-active {
  /* font-size: 14px;
  font-family: monospace; */
  width: 120px;
  overflow: hidden;
  font-size: 20px;
  font-family: sans-serif;
  color: rgba(255, 255, 255, 0.541);
}


@keyframes growBar {
  from {
    height: 0;
    transform: translateY(90);
  }
  to {
    height: 20vh;
    transform: translateY(0);
  }
}

@keyframes shrinkBar {
  from {
    height: 20vh;
    transform: translateY(0); /* Bar yukarıdan başlar */
  }
  to {
    height: 0;
    transform: translateY(90); /* Bar aşağıya doğru küçülür */
  }
}

.bar {
  background: linear-gradient(to right, currentcolor 1px, transparent 1px) 1px /
    1px calc(100% - 20px) no-repeat;
  padding-left: 7.5px;
  color: rgb(255 255 255);
}

.push-bar {
  animation: growBar 1s ease forwards;
}

.shrink-bar {
  animation: shrinkBar 1s forwards;
}
