/* Ana container için */
.starry-background {
    position: relative;
    width: 100%;
    height: 100vh;
    background: black;
    z-index: 0;
  }
  
  .star {
    position: absolute;
    width: 2px;
    height: 2px;
    background: white;
    border-radius: 100%;
    opacity: 0;
    z-index: -1;
    animation: twinkling 4s infinite ease-in-out;
  }
  
  @keyframes twinkling {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 0.6;
    }
    100% {
      opacity: 0;
    }
  }
  