.about-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 80%;
  position: relative; 
  z-index: 1;
}
.mid-section {
  position: relative;
}

.profile-img {
  /* width: 100%; */
  /* height: 100%; */
  /* border-radius: 50%; */
  /* object-fit: cover; */
  /* width: 100%;
  max-width: 100%;
  height: auto; 
  object-fit: contain; */
  overflow: hidden;
  width: 70%;
  margin-top: 10rem;
  margin-right: 5rem;
  opacity: 0.8;
  display: block;
  z-index: 10; /* Profile img'yi önde göstermek için yüksek bir z-index verdik */
  position: relative; /* Z-index'in çalışması için gerekli */
  mask-image: radial-gradient(circle, rgba(0, 0, 0, 1) 27%, rgba(0, 0, 0, 0) 85%);
  -webkit-mask-image: radial-gradient(circle, rgba(0, 0, 0, 1) 27%, rgba(0, 0, 0, 0) 85%);



}
.image-container {
  overflow: hidden;
}

.title-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 65%;
  right: 52%;
}

.title {
  font-size: 1.5rem;
  font-weight: 500;
  margin-bottom: 1rem;
  color: #ff9b00;
}

.user-name-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
  position: absolute;
  top: 45%;
  left: 52%;
}
.user-name {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: #ff9b00;
}

