.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  align-items: center;
  width: 100%;
  background-color: #fbfbfb;
  mix-blend-mode: difference;
}

.content {
  width: 100%;
  height: 100vh;
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  -ms-overflow-style: none; 
  scrollbar-width: none; 

}

.content::-webkit-scrollbar {
  display: none; /* Chrome; Safari ve Opera */
}

.section {
  height: 100vh;
  /* padding: 20px; */
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  scroll-snap-align: start;
  scrollbar-width: 100vh;
}

.section h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.section p {
  font-size: 1.2rem;
}
